<template>
  <div class="remember-pass">
    <v-container grid-list-md>
      <v-layout
        row
        wrap
      >
        <v-flex
          xs12
          md6
          offset-md3
        >
          <material-card
            color="green"
            title="Recuperar Clave"
            class="text-xs-center"
          >
            <form
              ref="rememberPassForm"
              v-if="form"
            >
              <v-alert
                :value="hasErr"
                :type="msgType"
                transition="scale-transition"
              >{{ msg }}</v-alert>
              <div class="text-xs-left">Por favor ingrese su email</div>
              <v-text-field
                v-model="email"
                name="email"
                label="Email"
                id="email"
                prepend-icon="mdi-email"
                @focus="hasErr = false"
                autocapitalize="none"
                :rules="emailRules"
                validate-on-blur
              ></v-text-field>
              <v-btn
                color="primary"
                class="ma-2"
                @click="send"
                small
                :loading="loading"
              >Enviar</v-btn>
            </form>
            <v-card-text v-else>
              {{sentMsg}}
              <v-btn
                class="mt-4"
                to="/dashboard"
                color="success"
                small
              >Regresar</v-btn>
            </v-card-text>
          </material-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      loading: false,
      sentMsg: '',
      form: true,
      email: '',
      msgType: 'error',
      hasErr: false,
      msg: '',
      emailRules: [
        v => !!v || 'Se requiere el email',
        v =>
          /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
          'Debe ser un email válido'
      ]
    }
  },
  methods: {
    send() {
      this.loading = true
      if (!this.email || this.email.trim().length === 0) {
        this.msgType = 'error'
        this.hasErr = true
        this.msg = 'Se requiere un email válido'
      }
      this.$store.dispatch('rememberPass', this.email).then(res => {
        this.form = false
        console.log(res)
        // this.sentMsg = `Se envió un email a ${
        //   res.data.data.rememberPass.email
        // } con instrucciones para recuperar su clave`
        // console.log(res)
        this.loading = false
      })
    }
  }
}
</script>

